.blockTitle {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 40px 0;
}

.lineBefore, .lineAfter {
    flex-shrink: 1;
    width: 100%;
    height: 2px;
    background: #474747;
}

.title {
    text-align: center;
    margin: 5px 15px 5px 15px;
    color: #474747;
    font-weight: 800;
    text-transform: uppercase;
    font-size: 50px;
}